@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
.main {
  font-family: 'Poppins', sans-serif;
  max-width: 1920px;
  height: 1px;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.button {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  cursor: pointer;
  background-color: rgba(255, 255, 255, .9);
  border: none;
  border-radius: 30px;
  outline: none;
  transition: background-color .5s ease;
}

.button:hover {
  background-color: rgba(255, 255, 255, .75);
  transition: background-color .5s ease;
}

.home__title {
  margin: 0 0 60px;
  font-size: 48px;
  line-height: 1.2;
  color: rgba(255, 255, 255, .9);
  z-index: 1;
}

.home__navlink {
  z-index: 1;
}

.home__button {
  width: 300px;
  height: 100px;
  border-radius: 50px;
}

.home__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .8);
}

.home {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: url(../../static/media/home-background.11c8786a.jpg) no-repeat center / cover;
  position: relative;
}

.map__left-column {
  width: 30%;
  border-right: #000 1px solid;
  overflow: scroll;
  background-color: #FFEFD5;
}

.map__right-column {
  width: 70%;
}

.map__header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: #FFE4B5	;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 20px;
  border-bottom: #000 1px solid;
}

.map__title {
  margin: 0;
}

.map__back-button {
  height: 50px;
  width: 100px;
  border: #000 1px solid;
  transition: box-shadow .25s ease;
}

.map__back-button:hover {
  box-shadow: #000 0 0 2px;
  transition: box-shadow .25s ease;
}

.map__coordinates {
  display: inline-block;
  background-color: #fff;
  position: absolute;
  top: 0;
  right: 10px;
  z-index: 1;
}

.map {
  display: flex;
  height: 100%;
  max-height: 100vh;
  width: 100%;
  overflow: scroll;
}

.point-list {
  margin: 0;
  padding: 20px 20px 20px 40px;
  list-style: circle;
}

.point__place-name {
  margin: 0 0 16px;
  outline: none;
  cursor: pointer;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}





